import React, { useEffect, useState, useRef } from "react";
import {
  ChoiceGroup,
  
  DefaultButton,
  
  PrimaryButton
} from "office-ui-fabric-react/lib/index";
import { MyTeams } from "../../helpers/OfficeGraph";
import "./style.css";
import { navigate } from "@reach/router";
export default function StartDatePage(props) {
  const [joinedTeams, setJoinedTeams] = useState([]);
  const [selectedTeam, setSelectedTeam] = useState(null);
  useEffect(() => {
    async function load() {
      {
        var teams = await MyTeams();

        var _joinedTeams = [];
        if (teams) {
          teams.map((team) => {
            _joinedTeams.push({ key: team.id, text: team.displayName });
          });

          setJoinedTeams(_joinedTeams);
        }
      }
    }

    load();
  }, []);

  return (
    <div className="homePage">
      <div>
        <div style={{minHeight:"50vh"}}>
        <ChoiceGroup  options={joinedTeams} onChange={(e,selected)=>{
          
          setSelectedTeam(selected)

        }} label="Select Team" required={true} />

        </div>
        <div className="speedDateActions">
            <DefaultButton    onClick={()=>{navigate(`/test`)}}>Previous</DefaultButton>
            <PrimaryButton  disabled={selectedTeam===null}  onClick={()=>{navigate(`/start/${selectedTeam.key}`)}}>Next</PrimaryButton>



        </div>
      </div>
    </div>
  );
}
